<template>
    <v-container fluid class="container" @click="timer_logout_set()">
        <layout-header/>
        <v-row class="contents" align="center" justify="center">
            <!-- 서초구 보건소 -->
            <list-menu-list
                v-if="!exerciseEmpty && site === '1'"
                class="content-body"
                :title="todayTitle"
                :switchItems="switchItems"
                :thumbnailList="thumbnailList"
            />
            <!-- 오늘의 운동 -->
            <list-menu-list
                v-else-if="!exerciseEmpty && site === '0'"
                class="content-body"
                :title="todayTitle"
                :description="todayDescription"
                :thumbnailList="thumbnailList"
            />
            <play-exercise-empty 
                v-else
                :exerciseEmptyItems="exerciseEmptyItems"
            />
        </v-row>
    </v-container>
</template>

<script>
import { mapActions, mapGetters, mapMutations } from 'vuex'
import exercise from '@/mixins/exercise.js'
import timer_logout from '@/mixins/timer_logout.js'

export default {
    name: 'Today',
    mixins:[ exercise, timer_logout ],
    data:()=>({
        thumbnailList: [],
        todayTitle: '오늘의 운동 목표',
        todayDescription: '운동을 시작하려면 해당 운동을 선택해주세요.',
        exerciseEmpty: false,
        exerciseEmptyItems:{
            title:'운동 처방이 없습니다.',
            description: '운동 처방을 받으면 나에게 맞춘 운동 프로그램에 따라 운동할 수 있어요.',
            description_1: '운동 처방을 받으려면 온핏 피트니스 직원의 도움을 받으세요.'
        },
        site: process.env.VUE_APP_SITE
    }),
    computed:{
        ...mapGetters([
            'GET_MENU_EX_RESULT_LIST',
            'GET_MENU_TODAY_LIST',
        ]),
        switchItems(){
            return this.exercise_free_weight_switch_items
        }
    },
    created() {
        this.CheckSite()
    },
    methods: {
        ...mapActions([
            'ACT_MENU_API_GET_EX_RESULT_LIST',
            'ACT_MENU_API_GET_TODAY_LIST',
        ]),
        ...mapMutations([
            'MU_MENU_TODAY_LIST',
        ]),
        CheckSite(){
            // 서초구 보건소 예외처리
            if(this.site === '1'){
                this.todayTitle = '서초구 보건소'
                this.exerciseEmptyItems ={
                    title: '준비된 운동이 없습니다.',
                    description: '운동프로그램이 준비 되면 프로그램에 따라 운동할 수 있습니다.'
                }
                this.GetExSeoChoList()
            }else{
                this.GetExResultList()
                this.GetExTodayList()
            }
        },
        GetExSeoChoList(){
            for(let exercise of this.exercise_seochogu_items){
                exercise.link       = 'PartPlay'
                exercise.switchType = 0

                // skeleton 체크
                let skeleton = this.exercise_skeleton_items.find(list=> list.exSortCode === exercise.exSortCode)
                if(skeleton !== undefined) exercise.skeleton = true

                // 이미지 추가
                exercise.image  = this.exercise_create_image(exercise.exSortCode)

                // video 추가
                exercise.video  = this.exercise_create_ex_video(exercise.exSortCode)

                this.thumbnailList.push(exercise)
            }
        },
        async GetExResultList(){
            // 운동 이력 가져오기
            const exType = 0 // 서킷을 제외한 나머지는 0 (프리웨이트, 오늘의 운동, 스트레칭)
            await this.ACT_MENU_API_GET_EX_RESULT_LIST(exType)
        },
        async GetExTodayList(){
            // 오늘의 운동 가져오기
            const searchType = 2 // 1= 모든 운동 2= mirror 처방운동
            await this.ACT_MENU_API_GET_TODAY_LIST(searchType)
            .then(()=>{
                this.EditTodayList()
            })
        },
        EditTodayList(){
            this.GET_MENU_TODAY_LIST.forEach(item => {
                item.link  = 'PartPlay'
                item.exSortCode = item.sortCode
                
                // 운동 이력 체크
                let check = this.GET_MENU_EX_RESULT_LIST.exList.find(list => list.exSortCode === item.exSortCode)
                if(check !== undefined) item.check = true

                // skeleton 체크
                let skeleton = this.exercise_skeleton_items.find(list=> list.exSortCode === item.exSortCode)
                if(skeleton !== undefined) item.skeleton = true

                // 이미지 추가
                item.image  = this.exercise_create_image(item.sortCode)

                // video 추가
                item.video = this.exercise_create_ex_video(item.exSortCode)

                this.thumbnailList.push(item)
            })
            this.MU_MENU_TODAY_LIST(this.thumbnailList)

            // 오늘의 운동 빈 항목인지 확인
            if(this.thumbnailList.length === 0) this.exerciseEmpty = true
        }
    },

}
</script>

<style scoped lang="scss">
</style>